import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Button from "@mui/material/Button";

const dispatchedEvents = [];

const SrsMuiButton = function (props, ref) {
  return (
    <Button
      variant={props.variant}
      color={props.color}
      size={props.size}
      disabled={props.disabled}
      onClick={props.onClick}
      startIcon={props.startIcon}
      endIcon={props.endIcon}
      fullWidth={props.fullWidth}
      theme={props.muiTheme}
    >
      <slot />
      {props.content}
    </Button>
  );
};

const styles = `
  /* Custom styles for the MuiButton component */
`;

const props = [
  {
    name: "variant",
    defaultValue: "contained",
    type: "string",
  },
  {
    name: "content",
    defaultValue: "",
    type: "string",
  },
  {
    name: "color",
    defaultValue: "primary",
    type: "string",
  },
  {
    name: "size",
    defaultValue: "medium",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "startIcon",
    defaultValue: null,
    type: "string",
  },
  {
    name: "endIcon",
    defaultValue: null,
    type: "string",
  },
  {
    name: "fullWidth",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiButton),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
